import navBar from '@/components/nav-bar.vue'
import mediaList from '@/components/mediaList.vue'
import productList from '@/components/product-list.vue'
import uniLoadMore from '@/components/uni-load-more.vue'
import {
	getKeyWord,
	getGoodDetail,
	getRelation_id
} from "@/api/goods.js"
export default {
	data() {
		return {
			isShow: false,
			result: null,
			keyword: '',
			goodsId:'',
			page: 1,
			loadingType: 0,
			contentText: {
				contentdown: "没有更多了",
				contentrefresh: "正在加载...",
				contentnomore: "正在加载..."
			},
		}
	},
	components: {
		navBar,
		productList,
		uniLoadMore,
		mediaList
	},
	onLoad(option) {
		this.keyword = option.keyword||'';
		this.goodsId=option.goodsId||'';
		console.log(JSON.stringify(option));
		this.getData();
	},
	onNavigationBarSearchInputChanged(e){
		this.getinput(e);
		console.log(e);
	},
	onNavigationBarSearchInputConfirmed(e){
		this.onTap(e.text);
	},
	//到底加载更多
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		console.log(this.page);
		let ret = getKeyWord(this.keyword, this.page);
		ret.then(res => {
			if (res.code == 200) {
				if (res.result.length == 0) {
					this.loadingType = 3;
					return
				}
				this.result = this.result.concat(res.result);
				this.page++;
				this.loadingType = 0;
			} else {
				this.loadingType = 3;
				uni.showToast({
					title: '没有更多了',
					icon:'none'
				})
				return
			}
		})
	},
	methods: {
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		getRelationId() {
			console.log(JSON.stringify(uni.getStorageSync("user")));
			try {
				if(uni.getStorageSync("user").pid==""){
					getRelation_id({
						phone:uni.getStorageSync("user").phone
					}).then(res=>{
						console.log(res.data);
						if(res.code==200){
							if(res.data==""){
								uni.showToast({
									title:'你还没有授权淘宝',
									icon:'none'
								})
								 return 0;
							}
							else return res.data;
						}
					})
				}else{
					return uni.getStorageSync("user").pid
				}
			} catch (e) {
				console.log(e);
			}
		},
		getData() {
			uni.showLoading({
				title: '搜索中'
			})
			if(this.goodsId==''){
				let ret = getKeyWord(this.keyword,1);
				ret.then(res => {
					console.log(JSON.stringify(res));
					uni.hideLoading();
					if (res.code == 200) {
						this.result = res.result;
						console.log('搜索结果', this.result);
					} else {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						})
					}
				})
			}else{
				console.log(this.getRelationId());
				getGoodDetail(Number(this.goodsId),Number(this.getRelationId())).then(res=>{
					uni.hideLoading();
					console.log(res.data);
					this.result=[];
					let goods=res.data.item_info;
					goods.item_id=res.data.item_id;
					goods.youhuiquan=res.data.youhuiquan;
					goods.commission_rate=res.data.max_commission_rate*100;
					this.result.push(goods);
					console.log(this.result[0]);
				})
			}
		},
		submit(e) {
			this.keyword = e.detail.value.trim();
			this.getData()
		},
		getinput(e){
			this.value = e.text;
			if (this.value) {
				this.isShow = true
			} else {
				this.isShow = false
			}
		},
		_getinput(e) {
			this.keyword = e.detail.value.trim();
			if (this.keyword) {
				this.isShow = true
			} else {
				this.isShow = false
			}
		},
		clear() {
			this.keyword = ''
			this.isShow = false;
		},
		onTap(value) {
			this.keyword = value
			this.isShow = true
			this.getData();
			// console.log(value)
		},
	}
}
